    .wx-menu-manage {
      font-size: 12px;
      width: 960px;
      margin: 14px 30px 0;
    }
    .preview-area {
      position: relative;
      float: left;
      margin-right: 12px;
    }
    .virtual-phone {
      position: relative;
      width: 317px;
      height: 560px;
      background: transparent url(../../images/bg/bg_mobile_head_default2968da.png) no-repeat 0 0;
      background-position: 0 0;
