      width: 590px;
      padding-left: 12px;
    }
    .editor-inner {
      min-height: 560px;
      padding-bottom: 20px;
      padding: 0 20px 5px;
      background-color: #f4f5f9;
      border: 1px solid #e7e7eb;
      border-radius: 0;
      box-shadow: none;
    }
    .editor-hd {
      padding: 9px 0;
      border-bottom: 1px solid #e7e7eb;
    }
    .editor-hd .title {
      font-weight: 400;
      float: left;
    }
    .editor-bd .frm-group {
      margin-bottom: 10px;
      margin-top: 30px;
    }
    .frm-group .frm-label {
      float: left;
      width: 5em;
      margin-top: .3em;
      margin-right: 1em;
      font-size: 14px;
    }
    .frm-group .frm-label .title {
      font-weight: 400;
      font-style: normal;
    }

    .editor-bd .frm-controls {
      display: table-cell;
      vertical-align: top;
      float: none;
      width: auto;
    }
    .frm-controls .frm-input-box {
      display: inline-block;
      position: relative;
      box-sizing: border-box;
      height: 30px;
      line-height: 30px;
      vertical-align: middle;
      width: 230px;
      font-size: 14px;
      padding: 0 10px;
      border: 1px solid #e7e7eb;
      box-shadow: none;
      border-radius: 0;
      background-color: #fff;
    }
    .frm-select .frm-input-box {
      padding: 0 0;
    }


    .frm-controls .frm-input-box.counter_in {
      width: 295px;
      padding-right: 10px;
    }
    .frm-controls .frm-input {
      height: 28px !important;
      line-height: 28px !important;
      /*margin: 4px 0;*/
      width: 100%;
      background-color: transparent !important;
      border: 0 !important;
      outline: 0 !important;
      padding: 0 !important;
      vertical-align: top;
    }
    .frm-controls .frm-select {
      width: 100%;
      height: 27px;
    }
    .frm-controls .frm-tips {
      color: #8d8d8d;
    }
    .frm-controls .frm-msg,
    .frm-controls .frm-tips {
      padding-top: 4px;
    }
    .frm-msg.fail {
      color: #e15f63;
    }
    .editor-ft {
      position: absolute;
      width: 100%;
      bottom: 10px;
      text-align: center;
    }
    #btn-delMenu {
      float: right;
      text-decoration: none;
      color: #03a9f4;
    }

	.frm-controls.frm-select .frm-input-box {
		border: none;
    	background: transparent;
    	line-height: 16px;
	}

    /* 菜单列表 */

    /* 目录类型 */
    [menu-type="dir"] .fa:before {
      content: "\f0c9";
    }
    [menu-type="dir"] .sub_menus {
      display: none;
      position: absolute;
      left: 0;
      width: 100%;
      border: 1px solid #d0d0d0;
      bottom: 60px;
      background-color: #fafafa;
      border-top-width: 0;
    }
    /* 主菜单选中子菜单展开 */
    [menu-type="dir"].selected .sub_menus,
    [menu-type="dir"].sub_selected .sub_menus {
      display: block;
    }
    .sub_menus {
      display: none;
    }
    .sub_menus .sub_menus_list {
      padding-left: 0;
      list-style-type: none;
    }
    .sub_menus .arrow {
      position: absolute;
      left: 50%;
      margin-left: -6px;
    }
    .sub_menus .submenu:first-child {
      border-top: 1px solid #d0d0d0;
    }
    .arrow.arrow_out {
      bottom: -6px;
      display: inline-block;
      width: 0;
      height: 0;
      border-width: 6px;
      border-style: dashed;
      border-color: transparent;
      border-bottom-width: 0;
      border-top-color: #d0d0d0;
      border-top-style: solid;
    }
    .arrow.arrow_in {
      bottom: -5px;
      display: inline-block;
      width: 0;
      height: 0;
      border-width: 6px;
      border-style: dashed;
      border-color: transparent;
      border-bottom-width: 0;
      border-top-color: #fafafa;
      border-top-style: solid;
    }




    /* 主菜单 */
    .menu {
      position: relative;
      line-height: 50px;
      float: left;
      text-align: center;
      border: 1px solid transparent;
      border-right: 1px solid #e7e7eb;
      cursor: pointer;
    }
    .menu > a {
      text-decoration: none;
      color: #c1c1c1;
      border-left: 1px solid #e7e7eb; 
    }
    .menu > a > .fa {
      margin-right: 5px;
    }

    /* 主菜单选中 */
    .menu.selected {
      border: 1px solid #44b549;
    }
    .menu.selected > a {
      color: #44b549;
    }

    /* 主菜单占位符样式 */
    .placeholder {

    }




    /* 子菜单 */
    .submenu {
      line-height: 44px;
      border: 1px solid transparent;
      margin: 0 -1px -1px;
    }
    .submenu > a {
      display: block;
      width: auto;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      word-wrap: normal;
      color: #c1c1c1;
      text-decoration: none;
      padding: 0 .5em;
      text-decoration: none;
    }
    .submenu .inner {
      display: block;
      border-top: 1px solid #e7e7eb;
      width: auto;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      word-wrap: normal;
      cursor: pointer;
    }

    /* 子菜单选中 */
    .submenu.selected {
      background-color: #fff;
      border: 1px solid #44b549;
    }
    .submenu.selected:first-child {
      border-top: 1px solid #44b549;
    }


    .submenu.selected > a {
      color: #44b549;
    }

    /* 子菜单占位符样式 */
    .sub—placeholder .fa:before{
      content: "\f067";
    }

    .frm-error {
      display: block;
      padding: 10px 15px;
      top: 0;
      left: 0;
      background-color: #FFF2B5;
      box-shadow: 0 1px 5px rgba(0,0,0,0.2);
      text-align: center;
      width: 100%;
      color: #e15f63;
      position: relative;
    }
    .frm-error .close {
      position: absolute;
      right: 10px;
      top: 10px;
      color: #666;
      cursor: pointer;
    }
